<template>
  <div class="User-container">
    <!-- 登录成功:顶部两行 -->
    <van-cell-group v-if="user" class="my-info">
      <van-cell
        class="base-info"
        title="单元格"
        value="内容"
        center
        :border="false"
      >
        <van-image
          class="avatar"
          slot="icon"
          round
          fit="cover"
          src="https://img.yzcdn.cn/vant/cat.jpeg"
        />
        <div class="name" slot="title">昵称</div>
        <van-button class="update-btn" size="small" round>编辑资料</van-button>
      </van-cell>
      <van-grid class="data-info" :border="false">
        <van-grid-item class="data-info-item">
          <div slot="text" class="text-wrap">
            <div class="count">123</div>
            <div class="text">头条</div>
          </div>
        </van-grid-item>
        <van-grid-item class="data-info-item">
          <div slot="text" class="text-wrap">
            <div class="count">123</div>
            <div class="text">关注</div>
          </div>
        </van-grid-item>
        <van-grid-item class="data-info-item">
          <div slot="text" class="text-wrap">
            <div class="count">123</div>
            <div class="text">粉丝</div>
          </div>
        </van-grid-item>
        <van-grid-item class="data-info-item">
          <div class="text-wrap" slot="text">
            <div class="count">123</div>
            <div class="text">获赞</div>
          </div>
        </van-grid-item>
      </van-grid>
    </van-cell-group>
    <!-- /顶部两行 -->

    <!-- 未登录:顶部 -->
    <div v-else class="not-login">
      <div @click="$router.push('/login')">
        <img class="mobile" src="./avatar.png" />
      </div>
      <div class="text">登录 / 注册</div>
    </div>
    <!-- /未登录显示的顶部 -->

    <!-- 平分一行: column-num="2" -->
    <van-grid class="nav-grid mb-4" :column-num="2">
      <van-grid-item
        class="nav-grid-item"
        icon-prefix="toutiao"
        icon="shoucang"
        text="收藏"
      />
      <van-grid-item
        class="nav-grid-item"
        icon-prefix="toutiao"
        icon="lishi"
        text="历史"
      />
    </van-grid>
    <van-grid :column-num="3" square>
      <van-grid-item icon="bill-o" text="优惠券" />
      <van-grid-item icon="location-o" text="地址管理" />
      <van-grid-item icon="warning-o" text="账号安全" />
    </van-grid>

    <van-cell title="消息通知" is-link to="/" />
    <van-cell class="mb-4" title="小智同学" is-link to="/" />
    <van-cell
      v-if="user"
      class="logout-cell"
      title="退出登录"
      @click="onLogout"
    />
  </div>
</template>

<script>
// 注意这里是vuex
import { mapState } from 'vuex'

export default {
  name: 'User',
  data() {
    return {}
  },
  computed: {
    ...mapState(['user'])
  },
  methods: {
    // 提示用户确认退出
    onLogout() {
      this.$dialog
        .confirm({
          title: '退户确认',
          message: '退出当前当前应用，将不能发布评论等'
        })
        .then(() => {
          // 确认执行这里
          // 清除容器和本地存储的数据
          this.$store.commit('setUser', null) // 不好
          this.$store.commit('removeItem')
          // // console.log(this.user)
        })
        .catch(() => {
          // 取消执行这里
        })
    }
  }
}
</script>

<style scoped lang="less">
.User-container {
  .my-info {
    background: url('./banner.png') no-repeat;
    background-size: cover;
    .base-info {
      box-sizing: border-box;
      height: 115px;
      background-color: unset;
      padding-top: 38px;
      padding-bottom: 11px;
      .avatar {
        box-sizing: border-box;
        width: 66px;
        height: 66px;
        border: 1px solid #fff;
        margin-right: 11px;
      }
      .name {
        font-size: 15px;
        color: #fff;
      }
      .update-btn {
        height: 16px;
        font-size: 10px;
        color: #666666;
      }
    }
    .data-info {
      .data-info-item {
        height: 65px;
        color: #fff;
        .text-wrap {
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          .count {
            font-size: 18px;
          }
          .text {
            font-size: 11px;
          }
        }
      }
    }
    /deep/ .van-grid-item__content {
      background-color: unset;
    }
  }

  .not-login {
    height: 180px;
    // background: url('./banner.png') no-repeat;
    background-color: #50a5fb;
    background-size: cover;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .mobile {
      width: 66px;
